import React, {
  useCallback, useEffect, useRef, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, FormLabel, PinInput, PinInputField, Button, Text, Link, createStandaloneToast, useStyleConfig
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import style from './AtmPin.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { getValutId, loading } from '../../../utils/general';
import { pinToken } from '../../../store/middlewares/card';
import { useDrawer } from '../../../hooks/drawer';

const AtmPin = () => {
  const form = useRef<any>();
  const { goBack } = useDrawer();
  const { t } = useTranslation();
  const [pin, setPin] = useState('');
  const [rePin, setRePin] = useState('');
  const dispatch = useAppAsyncDispatch();

  const mode = useAppSelector((store) => store.core.config.mode);
  const cardData = useAppSelector((store) => store.card.selectedCard, shallowEqual);

  const inputStyle:any = useStyleConfig('Input', {
    variant: 'primary'
  });

  const vgsInit = useCallback(() => {
    form.current = window.VGSCollect.create(getValutId(), mode === 'live' ? 'live' : 'sandbox', () => {

    });
    form.current.field('#dummy', {
      type: 'text',
      name: 'dummy',
      // hideValue: true
    });
  }, []);

  useEffect(() => {
    const vgsScript = document.createElement('script');
    vgsScript.id = 'vgs-collect-script';
    vgsScript.crossOrigin = 'anonymous';
    vgsScript.src = 'https://js.verygoodvault.com/vgs-collect/2.11.0/vgs-collect.js';
    vgsScript.type = 'text/javascript';
    vgsScript.onload = vgsInit;
    document.head.append(vgsScript);

    return () => {
      window.VGSShow = undefined;
      document.getElementById('vgs-collect-script')?.remove();
    };
  }, []);

  const onSubmit = useCallback(() => {
    const { toast } = createStandaloneToast();
    const allNums = '0123456789';
    const allNumsRev = '9876543210';

    if (pin !== rePin) {
      toast({
        title: 'An error occurred.',
        position: 'top-right',
        description: t('atm_pin.samePin'),
        status: 'error',
        duration: 9000,
        isClosable: true,
      });
    } else if (allNums.indexOf(pin) !== -1 || allNumsRev.indexOf(pin) !== -1 || pin.split(pin.charAt(0)).length - 1 === 4) {
      toast({
        title: 'An error occurred.',
        position: 'top-right',
        description: t('atm_pin.atmPinCombination'),
        status: 'error',
        duration: 9000,
        isClosable: true,
      });
    } else if (cardData) {
      const d = {
        pin,
        expiryMonth: cardData?.expiryMonth,
        expiryYear: cardData?.expiryYear,
        last4: cardData?.last4
      };

      dispatch(pinToken(cardData?.id))
        .then((res) => {
          const headers = { 'sd-pin-token': res.pinToken };
          loading(true);
          form.current.submit(`/v1/card/${cardData?.id}/pin`, {
            data: () => d,
            headers
          }, (status: number, response: any) => {
            if (status === 200 && response) {
              loading(false);
              toast({
                title: 'Success',
                position: 'top-right',
                description: t('atm_pin.pinSetSuccess'),
                status: 'success',
                duration: 9000,
                isClosable: true,
              });
              goBack();
            }
          }, () => {
            loading(false);
            toast({
              title: 'An error occurred.',
              position: 'top-right',
              description: 'Something went wrong and we could not process your request.',
              status: 'error',
              duration: 9000,
              isClosable: true,
            });
            // errors object:
            // {
            //  <invalid field name>: {
            //    <field state>
            //  },
            // }
          });
        });
    }
  }, [pin, rePin]);

  return (
    <div className="page">
      <MiniBox
        onBack={goBack}
        heading={t('atm_pin.title')}
        footer={(
          <>
            <Box pb="16px">
              <Text display="inline" variant="small" className="text-light">
                {t('atm_pin.footerDesc')}
                <br />
                <Link display="inline" variant="primary" href="https://www.solidfi.com/" isExternal>{t('atm_pin.learnMore')}</Link>
              </Text>
            </Box>
            <Button width="100%" variant="primary" onClick={onSubmit} disabled={pin.length !== 4 || rePin.length !== 4}>
              {t('atm_pin.ctaText')}
            </Button>
          </>
)}
      >
        <Box width="100%" paddingTop="15px">
          <div id="dummy" className="field-wrapper" style={{ display: 'none' }} />
          <FormLabel variant="primary" mb="16px">
            {t('atm_pin.enterPin')}
          </FormLabel>
          <Box mb="32px">
            <PinInput
              placeholder=""
              mask
              onChange={setPin}
              value={pin}
            >
              <PinInputField className={style.input_field} style={inputStyle.field} />
              <PinInputField className={style.input_field} style={inputStyle.field} />
              <PinInputField className={style.input_field} style={inputStyle.field} />
              <PinInputField className={style.input_field} style={inputStyle.field} />
            </PinInput>
          </Box>
          <FormLabel variant="primary" mb="16px">
            {t('atm_pin.reEnterPin')}
          </FormLabel>
          <Box mb="32px">
            <PinInput placeholder="" mask onChange={setRePin} value={rePin}>
              <PinInputField className={style.input_field} style={inputStyle.field} />
              <PinInputField className={style.input_field} style={inputStyle.field} />
              <PinInputField className={style.input_field} style={inputStyle.field} />
              <PinInputField className={style.input_field} style={inputStyle.field} />
            </PinInput>
          </Box>
        </Box>
      </MiniBox>
    </div>
  );
};

export default AtmPin;
